html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Montserrat", sans-serif;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 0 0 5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img, picture {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.boton-amarillo {
  background-color: #e08709;
  color: #ffffff;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
}
.boton-amarillo:hover {
  background-color: rgb(245.2854077253, 149.2188841202, 13.2145922747);
}

.boton-amarillo-block {
  background-color: #e08709;
  color: #ffffff;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
}
.boton-amarillo-block:hover {
  background-color: rgb(245.2854077253, 149.2188841202, 13.2145922747);
}

.boton-verde {
  background-color: #71b100;
  color: #ffffff;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
}
.boton-verde:hover {
  background-color: rgb(129.2796610169, 202.5, 0);
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

/* SECTION header */
/* Si el navegador NO soporta WebP, usa imagen JPG como fondo */
.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

/* Si el navegador SÍ soporta WebP, usa imagen WebP (más ligera) como fondo */
.webp .inicio {
  background-image: url(../img/header.webp);
}

/* Estilos para el header general */
.header {
  background-color: #333333; /* Usa color gris oscuro definido en variables */
  padding: 1rem 0 3rem 0; /* Espaciado interno: arriba 1rem, derecha 0, abajo 3rem, izquierda 0 */
  /* El & se refiere a este mismo selector, es decir .header */
}
.header.inicio {
  /* Esto aplica a .header.inicio, es decir, cuando tiene ambas clases */
  background-position: center center; /* Centra la imagen de fondo */
  background-size: cover; /* Hace que la imagen de fondo cubra todo el área */
  /* Mixin para pantallas tablet (usa media queries) */
  /* Estilos para el contenedor interno dentro de .header.inicio */
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem; /* Altura mínima de 70rem en pantallas tablet */
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem; /* También fija altura mínima en tablet dentro del contenido */
  }
}

/* Contenedor principal del contenido dentro del header */
.contenido-header {
  display: flex; /* Usa flexbox para organizar los elementos hijos */
  flex-direction: column; /* Ordena en columnas (de arriba hacia abajo) */
  justify-content: space-between; /* Deja espacio entre los elementos */
  height: 100%; /* Ocupa todo el alto disponible del contenedor */
  /* Estilos para el título principal dentro del header */
}
.contenido-header h1 {
  text-align: left; /* Alinea el texto a la izquierda */
  color: #ffffff; /* Usa color blanco definido en variables */
  font-size: 2.5rem; /* Tamaño de fuente 2.5rem */
  line-height: 2.5rem; /* Altura de línea 2.5rem para mantener proporción */
  max-width: 400px; /* Limita el ancho máximo a 400px para que no se expanda demasiado */
}

/* --- EXPLICACIÓN EXTRA --- */
/*
& → En Sass, el símbolo & significa “en este mismo selector”.
Por ejemplo:
.header {
  &.inicio { ... }
}
es lo mismo que escribir:
.header.inicio { ... }
*/
/*
: → Se usa para pseudo-clases, que son estados especiales de los elementos.
Ejemplo: :hover (cuando pasas el mouse), :focus (cuando haces clic en un campo).
*/
/*
:: → Se usa para pseudo-elementos, que son partes virtuales creadas por CSS.
Ejemplo: ::before (algo que aparece antes), ::after (algo que aparece después).
Esto te permite añadir decoración sin agregar más elementos al HTML.
*/
/* !SECTION fin - header */
.footer {
  padding: 2.5rem;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .contenedor--footer {
  padding-top: 2.5rem;
}
.footer .copyrigth {
  color: #ffffff;
  text-align: center;
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
  }
}
.navegacion a {
  color: #ffffff;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.anuncio {
  background-color: rgb(237.75, 237.75, 237.75);
  border: 1px solid rgb(199.5, 199.5, 199.5);
  margin-bottom: 2rem;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio h3,
.anuncio p {
  margin: 0;
}
.anuncio .precio {
  font-weight: bold;
  color: #71b100;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
}
.iconos-caracteristicas li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.iconos-caracteristicas li p {
  font-weight: 900;
}

.no-webp .imagen-contacto {
  background-image: url(../img/encuentra.jpg);
}

/* Si el navegador SÍ soporta WebP, usa imagen WebP (más ligera) como fondo */
.webp .imagen-contacto {
  background-image: url(../img/encuentra.webp);
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  color: #ffffff;
  font-weight: 900;
  font-size: 4rem;
}
.imagen-contacto p {
  font-size: 1.8rem;
  color: #ffffff;
}

.testimonial {
  background-color: #71b100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #ffffff;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #ffffff;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.texto-entrada a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71b100;
  margin-top: 1rem;
}
.texto-entrada span {
  color: #e08709;
}