@use 'variables' as v;
html {
  font-size: 62.5%; //1rem = 10px
  box-sizing: border-box; //hereda el tamaño de la caja
}
*,
*:before,
*:after {
  box-sizing: inherit; //hereda el tamaño de la caja
}
body {
  font-size: 1.6rem;
  font-family: v.$fuente_principal;
  &.fijar-body {
    overflow: hidden;
  }
  &.body-scroll {
    padding-top: 8.6rem;
  }
}
p {
  font-size: 2rem;
  color: v.$negro;
  line-height: 1.5;
}
.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}
a {
  text-decoration: none;
}
h1,
h2,
h3 {

  margin: 0 0 5rem 0;
   font-weight: 300;
      text-align: center;
}
h1 {
  font-size: 4rem;
}
h2 {
  font-size: 3.6rem;
}
h3 {
  font-size: 3rem;
}
img,picture {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}
